import React from "react";
import {
  Box,
  Grid,
  Typography,
  Button as MuiButton,
  TextField,
  TextareaAutosize,
  FormControlLabel,
  Checkbox,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import StandartContainer from "../../components/StandartContainer";
import cx from "classnames";

const useStyles = makeStyles((theme) => ({
  m3: {
    margin: `${theme.spacing(3)}px 0`,
    marginBottom: theme.spacing(6),
  },
  typesContainer: {
    "& > div": {
      marginBottom: theme.spacing(1),
    },
  },
  dFlex: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  btn: {
    textTransform: "capitalize",
    textDecoration: "underline",
    "&:hover": {
      textDecoration: "underline",
    },
  },
  textArea: {
    padding: theme.spacing(2),
    width: "100%",
    fontSize: "1rem",
    fontFamily: theme.typography.fontFamily,
  },
}));

const CreateGoods = () => {
  const classes = useStyles();

  return (
    <Grid container justify="center">
      <Grid item xs={8} xl={7}>
        <StandartContainer title=" " badge=" ">
          <Grid container className={classes.m3}>
            <Grid item xs={10}>
              <Box mb={3}>
                <Typography variant="h6" component="h3">
                   
                </Typography>
                <Grid container>
                  <Grid item xs={8} className={classes.typesContainer}>
                    <TextField fullWidth label="" />
                    <TextField fullWidth label="" />
                    <TextField fullWidth label="" />
                    <TextField fullWidth label="" />
                    <TextField fullWidth label=" " />
                    <TextField fullWidth label="" />
                  </Grid>
                </Grid>
              </Box>

              <Box mb={3}>
                <div className={classes.dFlex}>
                  <Typography variant="h6" component="h3">
                    
                  </Typography>
                  <MuiButton size="large" className={classes.btn}>
                     
                  </MuiButton>
                </div>
                <Grid container spacing={2}>
                  <Grid item xs={2}>
                    <TextField fullWidth label="" />
                  </Grid>
                  <Grid item xs={2}>
                    <TextField fullWidth label="" />
                  </Grid>
                  <Grid item xs={8}>
                    <TextField fullWidth label="" />
                  </Grid>
                  <Grid item xs={8}>
                    <TextField fullWidth label="" />
                  </Grid>
                </Grid>
              </Box>

              <Box mb={3}>
                <Typography variant="h6" component="h3">
                  
                </Typography>
                <Grid container spacing={4}>
                  <Grid item xs={8}>
                    <TextField fullWidth label="" />
                  </Grid>
                  <Grid item xs={12}>
                    <TextareaAutosize
                      className={classes.textArea}
                      rowsMin={6}
                      placeholder=" "
                    />
                    <Box mt={1} p={2} bgcolor="text.disabled" />
                  </Grid>
                </Grid>
              </Box>

              <Box mb={3}>
                <Typography variant="h6" component="h3">
                   
                </Typography>
                <Grid container>
                  <Grid item xs={6} container direction="column">
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  1"
                    />
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  2"
                    />
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  3"
                    />
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  4"
                    />
                  </Grid>
                  <Grid item xs={6} container direction="column">
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  5"
                    />
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  6"
                    />
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  7"
                    />
                    <FormControlLabel
                      control={<Checkbox name="checkedA" />}
                      label="  8"
                    />
                  </Grid>
                </Grid>
              </Box>

              <Box mb={3}>
                <Typography variant="h6" component="h3">
                  
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </StandartContainer>
      </Grid>
    </Grid>
  );
};

export default CreateGoods;
