import React from "react";
import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Container from "../../components/StandartContainer";
import ProductItem from "./ProductItem";
import NavLeft from "./NavLeft";

const useStyles = makeStyles((theme) => ({
  fsOblique: {
    fontStyle: "oblique",
  },
  mt2: {
    marginTop: theme.spacing(3),
  },
  header: {
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  moreBtn: {
    textDecoration: "underline",
    "&:hover": {
      textDecoration: "underline",
    },
  },
}));

const mockData = [
  {
    imgUrl: "images/products/9.webp",
    productName: "  ",
    brandName: "GUCCI",
    oldPrice: "90 000 .",
    price: "70 000 .",
    status: {
      code: 1,
      label: "  ",
    },
  },
  {
    imgUrl: "images/products/6.jpg",
    productName: "  ",
    brandName: "ARMANI",
    oldPrice: "190 000 .",
    price: "130 000 .",
    status: {
      code: 2,
      label: " ",
    },
  },
  {
    imgUrl: "images/products/5.webp",
    productName: " ",
    brandName: "DOLCE&GABANN",
    oldPrice: "55 000 .",
    price: "26 000 .",
    status: {
      code: 3,
      label: " ",
      info:
        "  28.05.2020  27.05.2020  9:00  18:00 \n  :  ,   36,  65",
    },
  },
  {
    imgUrl: "images/products/2.webp",
    productName: "  ",
    brandName: "GUCCI",
    oldPrice: "90 000 .",
    price: "70 000 .",
    status: {
      code: 4,
      label: "",
      info: " 28.05.2020",
    },
  },
];

const Goods = () => {
  const classes = useStyles();
  const links = [
    { label: " ", count: 10 },
    { label: " ", count: 100 },
    { label: " ", count: 1000 },
    { label: " ", count: 10 },
    { label: " ", count: 10 },
    { label: " ", count: 0 },
  ];
  return (
    <Grid container justify="center">
      <Grid item xs={10} spacing={2} container className={classes.root}>
        <Grid item xs={3}>
          <NavLeft links={links} />
        </Grid>
        <Grid item xs={9}>
          <Container title=" " badge=" ">
            {mockData.map((item, idx) => {
              return <ProductItem key={idx} {...item} />;
            })}
          </Container>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default Goods;
